.favorites-wrapper {
	flex-direction: column;
	display: flex;
}

.favorites {
	display: flex;
	gap: 25px;
	overflow: auto;
	margin-bottom: 30px;
}

.items {
	display: flex;
	gap: 25px;
	margin-bottom: 30px;
	justify-content: center;
	flex-wrap: wrap;
}

.loading,
.no-items {
	font-size: 40px;
	text-transform: uppercase;
	color: #ffff;
}
.no-items {
	display: flex;
	justify-content: center;
	width: 100%;
}

.favorites-title {
	color: #fa6400;
	font-weight: 700;
	font-size: 35px;
	margin: 12px 0 12px 0px;
	margin: 20px 0px;
}

.search-favorites input {
	padding: 10px 50px;
	margin-bottom: 30px;
}
